.module_block {
  display: block;
  position: relative;
  min-height: 50vh;
  background: rgba(0, 185, 223, 0.2); }
  .module_block .title {
    display: block;
    position: absolute;
    top: 50%;
    line-height: 40px;
    margin-top: -20px;
    left: 50%;
    width: 260px;
    margin-left: -130px;
    color: #224482;
    z-index: 2;
    text-transform: uppercase;
    letter-spacing: 5px;
    border: 1px dotted #224482; }
  .module_block .module-count {
    display: block;
    position: absolute;
    right: -15px;
    top: -15px;
    width: 30px;
    height: 30px;
    line-height: 30px;
    border-radius: 15px;
    text-align: center;
    letter-spacing: 0;
    background: #00b9df;
    color: #fff;
    font-size: 80%;
    font-family: sans-serif; }

