.module_image {
  display: block;
  position: relative; }
  .module_image .image {
    display: block;
    position: relative;
    width: 100%;
    height: auto; }
  .module_image .title {
    display: block;
    position: absolute;
    top: 50%;
    line-height: 40px;
    margin-top: -20px;
    left: 50%;
    width: 260px;
    margin-left: -130px;
    color: #fff;
    z-index: 2;
    text-transform: uppercase;
    letter-spacing: 5px;
    border: 1px dotted #fff; }

